import ModalBg from '@/components/base/modal-bg'
export default {
  components: {
    ModalBg
  },
  data() {
    return {
      modalShow: false
    }
  },
  props: {
    clickBgClose: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    closeModal() {
      if (!this.clickBgClose) {
        return false
      }
      this.modalShow = false
    },
    show() {
      this.modalShow = true
    },
    hide() {
      this.modalShow = false
    }
  }
}



